@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  background-color: #CACAD1;
  border-radius: 1rem;
  box-shadow: 1rem 1rem 1rem #000000;
  display: grid;
  grid-template: 
    'a a a' auto
    'c c c' auto
    'd d d' 1fr
    'e e e' auto
    / auto auto auto;
  margin: 20px 0;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  width: 300px;
}

.card:hover {
  box-shadow: 3rem 3rem 1rem rgba(0,0,0,0.5);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
}

.soda-pop {
  white-space: nowrap;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  font-size: 1.5rem;
  margin: 0;
}

.movie-title {
  border-radius: 0.75rem;
  display: flex;
  align-content: center;
  justify-content: space-evenly;
  grid-area: a;
  margin: 0.5rem 1rem;
  padding: 0.5rem 0;
  -webkit-transition: background-color .5s, color .5s;
  transition: background-color .5s, color .5s;
}

img {
  grid-area: c;
  cursor: pointer;
}

.jello {
  position: relative;
  overflow: hidden;
  height: 450px;
  width: 300px;
  z-index: 1;
}

.jello:hover::before {
  cursor: pointer;
  right: 0;
  -webkit-transition: right .5s cubic-bezier(0.73, 0.11, 0.22, 0.87);
  transition: right .5s cubic-bezier(0.73, 0.11, 0.22, 0.87);
}

.jello::before {
  display: flex;
  align-items: center;
  text-align: center;
  content: 'Click to add this movie to your favorites!';
  position: absolute;
  top: 200px;
  right: 300px;
  background-color: black;
  color: #CACAD1;
  height: 50px;
  width: 300px;
  z-index: 9;
  -webkit-transition: all .5s cubic-bezier(0.73, 0.11, 0.22, 0.87);
  transition: all .5s cubic-bezier(0.73, 0.11, 0.22, 0.87);
}

.overview {
  grid-area: d;
  padding: 0 1em;
}

.release_date {
  align-self: end;
  justify-self: center;
  grid-area: e;
}

.popcorn-show {
  align-self: center;
  height: 1.5rem;
  width: 1.5rem;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.popcorn-hide {
  display: none;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.has-color {
  background-color: #57282F;
  color: #CACAD1;
  -webkit-transition: background-color .5s, color .5s;
  transition: background-color .5s, color .5s;
}
.card-container {
  background-color: #990000;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  padding: 2rem 0 2.5rem 0;
}
header {
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  grid-template-areas: 'b a e' 'c . .' 'd . .';
  height: 100px;
  background: #990000;
  padding: 10px;
}
.title {
  grid-area: a;
  margin: 0;
  justify-self: center;
  text-decoration: none;
  font-family: 'Roboto Slab', serif;
  color: #CACAD1;
  font-size: 3em;
  text-shadow: -5px 0 4px black, 0 1px black, 1px 0 black, 0 -1px black;
}
.welcome {
  margin: 0;
  grid-area: b;
  text-decoration: none;
  color: #CACAD1;
  text-shadow: 3px 2px black, 1px 2px 2px black;
  font-size: 1.5em;
}
.favorites {
  grid-area: e;
  text-decoration: none;
  color: #CACAD1;
  text-shadow: 3px 2px black, 1px 2px 2px black;
  font-size: 1.5em;
  width: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  justify-self: center;
}
.log-link {
  grid-area: c;
  text-decoration: none;
  color: #CACAD1;
  text-shadow: 3px 2px black, 1px 2px 2px black;
  font-size: 1.5em;
  width: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10%;
}
.log-out {
  grid-area: c;
  text-decoration: none;
  color: #CACAD1;
  text-shadow: 3px 2px black, 1px 2px 2px black;
  font-size: 1.5em;
  width: 135px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10%;
}
.fa-sign-in-alt {
  margin-left: 1%;
}
form {
  grid-area: d;
}
